
import Vue from 'vue'
import { getQuestionDetail, submitAnswerZan } from '@/api/detail.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name: 'questions',
components: {
Tab,
TabItem,
},
data () {
return {
questionid:'',
detaillist:'',
status:true,
productid:'',
skuid:'',
}
},
methods: {
detail(){
const postData = {
questionid:this.questionid
}
getQuestionDetail(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.detaillist = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(res => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
zan(id,index){
const postData = {
answerid:id
}
submitAnswerZan(postData)
.then(res => {
if (res.data.errorcode === 0) {
if(this.status){
this.detaillist.useranswerlist[index].zannum = this.detaillist.useranswerlist[index].zannum*1+1
this.detaillist.useranswerlist[index].selfzan = 1
}
else{
this.detaillist.saleanswerlist[index].zannum = this.detaillist.saleanswerlist[index].zannum*1+1
this.detaillist.saleanswerlist[index].selfzan = 1
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(res => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
goanswer(){
window.localStorage.setItem("questiontext",this.detaillist.content)
this.$router.push({path:"/orderDetail/answer",query:{'questionid':this.questionid,'productid':this.productid,'skuid':this.skuid}})
},
useranswerlist(index){
if(index==1){
this.status = true
}
else{
this.status = false
}
},
goissue(){
this.$router.push({path:"/orderDetail/questions",query:{'productid':this.productid,'skuid':this.skuid}})
},
previewset (list,index) {
const that= this
that.$wechat.previewImage({
current: list[index], // 当前显示图片的http链接
urls: list 
});
},
gotodetail(){
this.$router.push({path: '/orderDetail', query: {'productid': this.productid,'skuid': this.skuid}})
}
},
created () {
this.questionid = this.$route.query.questionid
this.productid = this.$route.query.productid
this.skuid = this.$route.query.skuid
this.detail()
},
mounted(){
const that = this
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: "EDIFIER问答",
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/questionsdetail?questionid='+that.questionid+'&productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: "EDIFIER问答",
desc: '',
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/questionsdetail?questionid='+that.questionid+'&productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})
})
}
}



